<template>
    <div class="me">
        <mt-header fixed title="我的"></mt-header>
        <!-- 我的订单 -->
        <div class="touxiang">
            <mt-cell :title="username">
                <span class="iconfont">
                    <router-link v-show="is_login"  :to="{name:'Login'}">登陆/</router-link>
                    <router-link v-show="is_login" :to="{name:'Login'}">注册</router-link>
                    <span @click="zhuxiao" v-show="!is_login">注销</span>
                </span>
                <img slot="icon" src="../assets/denglu.png" width="24" height="24">
              </mt-cell>
        </div>
        <div class="dingdan">
            <mt-cell
            title="我的订单"
            :to="{name:'OrderList',params:{id:'1'}}"
            is-link
            value="全部订单">
            </mt-cell>
            <!--  -->
            <div class="dd_fenlei">
                <!-- { name:'RecomDetail',params:{id:item.id} -->
                <router-link :to="{name:'OrderList',params:{id:'2'}}">
                    <span class="iconfont">&#xe609;</span>
                    <p>待付款</p>
                </router-link>
                <router-link :to="{name:'OrderList',params:{id:'3'}}">
                    <span class="iconfont">&#xe68e;</span>
                    <p>待发货</p>
                </router-link>
                <router-link :to="{name:'OrderList',params:{id:'4'}}">
                    <span class="iconfont">&#xe66a;</span>
                    <p>待收货</p>
                </router-link>    
                <router-link :to="{name:'OrderList',params:{id:'5'}}">           
                    <span class="iconfont">&#xe63f;</span>
                    <p>已完成</p>
                </router-link>
            </div>

        </div>

        <!-- 帮助 -->
        <div class="bangzhu">
            <mt-cell title="收货地址管理" :to="{name:'Address'}" is-link value=""></mt-cell>
            <!-- <mt-cell title="帮助中心" :to="{name:'OrderList'}" is-link value=""></mt-cell> -->
            <mt-cell title="关于商城" @click="getbanben" is-link>
                <span @click="">1.0.0</span>
            </mt-cell>
        </div>
        <!-- <div>
            <router-link :to="{name:'test'}">
                闹着玩的一个小心坑
            </router-link>
        </div> -->


        <div class="foot">
            <Footer :foottab='3'></Footer>
        </div>
    </div>
</template>

<script>
import Footer from './common/Footer.vue'
import { Toast } from 'mint-ui';
import { Swipe, SwipeItem } from 'mint-ui';
import { MessageBox } from 'mint-ui';
export default {
  inject:['reload'],
  components:{
    Footer
  },
  data () {
    return {
        username: '未登录',
        is_login: true
    }
  },
  created(){
    if(localStorage.getItem("userphone")){
        this.username=localStorage.getItem("userphone")
        this.is_login=false
    }else{
        this.username = '未登录'
        this.is_login=true
    }
  },
  methods:{
    getbanben(){
      let instance = Toast('提示信息');
      setTimeout(() => {
        instance.close();
      }, 2000);
    },
    zhuxiao(){
        MessageBox.confirm('确定注销登录？').then(action => {
            localStorage.removeItem("userphone");
            this.username = '未登录'
            this.is_login=true
            // this.reload();
        });
        
    }
  }
}
</script>

<style scoped>
    .me{
        padding-top: 40px;
        /* padding: 40px 8px; */
        background-color: #f6f6f6;
    }
    .foot{
    width: 100%;
    height: 45px;
    background-color: rgb(255, 80, 80);
    position: fixed;
    bottom: 0;
    left: 0;
  }
  .touxiang{
      /* padding-top: 10px; */
  }
  .mint-cell-title{
      border-radius: 50px;
      background-color: teal;
  }
  .mint-cell img {
      /* background-color: rgb(217, 244, 252); */
    width: 30px;
    height: 30px;
    border-radius: 50px;
    vertical-align: middle;
    margin-right: 10px;
    }
    .mint-cell-value span {
        font-size: 0.875rem;
        color: #555;
    }
    .mint-cell-value a{
        font-size: 0.875rem;
        color: #555;
    }

    .mint-cell{
        min-height: 53px;
    }

.dingdan{
    background-color: white;
    padding: 5px;
    /* border-radius: 10px; */
    margin: 5px 0;
}

    .dd_fenlei{
        padding: 5px;
        padding: 15px 5px;
        /* 设置为弹性容器 */
        display: flex;
        /* 换行，第一行在上方 */
        flex-wrap: wrap;
        /* 间隔相等 ，自动分配距离*/
        justify-content: space-around;
        align-items: center; 
        border-top: 1px solid #f8f8f8;
    }
    .dd_fenlei a{
        flex-grow: 1;
        display: block;
        /* background-color: rgb(41, 112, 88); */
        text-align: center;
    }
    .dd_fenlei a span{
        font-size: 1.5rem;
    }
    .dd_fenlei a p{
        font-size: 0.875rem;
    }





    .bangzhu{
        /* border-top: 5px solid #f8f8f8; */

    }


</style>



